
import Swiper from 'vux/src/components/swiper/swiper.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import base64 from 'vux/src/tools/base64/index.js'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import AppHeader from './Header'
import AppFooter from './Footer'

import axios from 'axios'
import qs from 'qs'
import stringify from 'fast-json-stable-stringify'

import {loginApi,initApi,randomUserApi} from '../utils/apis'
import {getUserToken, setUserToken, clearUserToken, getPath} from '../utils/common'


export default {
directives: {
TransferDom
},
components: {
AppHeader,
AppFooter,
Swiper,
XInput,
XButton,
Group,
Cell,
Box,
Toast,
Loading,
CheckIcon
},
data () {
return {
loginDisabled:false,
loginShowLoading:false,
showToast:false,
toastType:'',
toastText:'',
showLoading:false,
loadingText:'',
confirmShow:false,
confirmText:'',
agree:false,
xm:'',
xh:'',
password: '',
banner:[{
url: 'http://xgb.chd.edu.cn/',
img: 'http://cdn.knocks.tech/banner/1.jpg',
title: '2018'
}, {
url: '/Map',
img: 'http://cdn.knocks.tech/banner/2.jpg',
title: ''
}, {
url: 'https://www.yiban.cn/org/orglistShow/type/forum/puid/5370552',
img: 'http://cdn.knocks.tech/banner/3.jpg', //
title: '',
}],
banner_index: 0,
}
},
mounted:function(){
},
methods:{
bannerChange (index) {
this.banner_index = index
},
loginButton(){
let _this = this;

_this.loginShowLoading = true;
_this.loginDisabled = true;

if(_this.agree === false){
_this.toastType = "text";
_this.toastText = "~";
_this.showToast = true;
_this.loginShowLoading = false;
_this.loginDisabled = false;
return false;
}
let formdata = {
'XH':_this.xh,
'SFZH':_this.password,
};
let postval = "key="+base64.encode(encodeURI(stringify(formdata)));
axios.post(loginApi, postval)
.then(function(res){

_this.loginShowLoading = false;
_this.loginDisabled = false;
if(res.data.code === 0){
_this.toastType = "cancel";
_this.toastText = res.data.data;
_this.showToast = true;
}
if(res.data.code === 1){
setUserToken(res.data.data[1]);
if(getUserToken() && res.data.data[0]){
let nextPath = getPath(res.data.data[0]);
_this.$router.push({path: nextPath});
}
}
})
.catch(function(err){
_this.toastType = "text";
_this.toastText = "..";
console.log(err);
});
},
buidingMaps(){
this.$router.push({path:'/Map'});
}
}
}



